import { UrlHelper } from '@gtLibTs/h5GameTools/urlHelper';

export class UserBehavior {
    static get shared() {
        if (window.gtLibTsScope?.userBehavior) {
            return window.gtLibTsScope.userBehavior;
        }
        return (window.gtLibTsScope ??= {}).userBehavior = new UserBehavior();
    }

    public sendGtagEvent(eventName: string, data: Record<string, any> = {}) {
        window.gtag?.('event', `customEvt_${eventName}`, {
            ...data,
            GameType: UrlHelper.shared.gameType
        });
    }
}